<script setup>
import { mdiCrosshairs, mdiCrosshairsGps } from '@mdi/js';
import Icon from '@/components/Svg.vue';
const props = defineProps({
  parent: {
    type: String,
    required: true
  },
  child: {
    type: String,
    required: true
  },
  year: {
    type: String,
    required: true
  },
  choukai: {
      type: Object,
      required: true
  },
});
const imgHtml = (url)=> {
  let html = '';
  let full = require(`@/assets/${props.year}/choukai/${url}`);
  html+= `<img class="px-5 mx-auto w-full md:w-1/3" src="${full}" alt="photo">`;
  return html;
}
</script>

<template>
  <!-- <div v-if="data.parent=='mondai1'" class="pt-14"></div> -->
  <div v-if="parent=='mondai1'" class="pt-3">
    <!-- mondai title -->
    <div
      class="font-bold px-3 pt-2 pb-4 lg:pb-8 text-sm text-justify select-none"
    > {{ choukai[parent].title }}
    </div>
    <!-- loop -->
    <div v-for="(question, index) in choukai[parent].questions" :key="index">
      <!-- check current -->
      <div v-if="parent=='mondai1' && child==(index+1)" > <!-- ပေါ့ကြီးကြီး -->
        <!-- title -->
        <div class="px-3 py-4 font-bold text-sm select-none">{{question.number}}・ばん</div>
        <!-- img injection -->
        <div v-html="imgHtml(question.question)"></div>
        <!-- answer box -->
        <div class="px-5 pt-3 grid grid-cols-2 gap-2 md:gap-6 select-none">
          <div 
            v-for="(answer,i) in question.answers"
            v-bind:key="i"
          >
            <button
              v-if="question.pick!=answer"
              v-on:click="choukai[parent].questions[index].pick=answer"
              class="flex flex-row w-full h-12 px-3 bg-gray-200 items-center justify-start rounded-md">
              <Icon :path="mdiCrosshairs" size="20" class="text-gray-500"/>
              <div class="pl-2 text-xs text-left">{{answer}}</div>
            </button>
            <button 
                v-if="question.pick==answer"
                class="flex flex-row w-full h-12 px-3 font-bold bg-gray-300 items-center justify-start rounded-md">
                <Icon :path="mdiCrosshairsGps" size="20" class="text-blue-500 font-bold"/>
                <div class="pl-2 text-xs　text-left">{{answer}}</div>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div v-if="parent=='mondai1'" class="my-12"></div>
</template>

