<template>
    <button 
        @click="toRoute()"
        class="w-3/4 md:w-2/3 my-4 py-2 bg-gradient-to-r from-indigo-500 to-blue-500 font-bold text-white text-xl text-center rounded-xl">
        {{text}} <Icon v-if="check" :path="mdiLock" size="20" class="text-yellow-400 text-xl ml-3"/>
    </button>
</template>

<script>
import { mdiLock } from '@mdi/js';
import Icon from '@/components/Svg.vue';
export default {
  data: ()=> ({ mdiLock }),
  components: {
    Icon
  },
  name: 'Button',
  methods: {
    toRoute: function(){
      if(this.free){
        this.$store.dispatch('setCurrentYear',this.param.year);
        this.$store.dispatch('setCurrentTitle',this.param.title);
        this.$router.push({ name: this.name, params: this.param });
      }else{
        if(this.$store.state.haveLicense){
          this.$store.dispatch('setCurrentYear',this.param.year);
          this.$store.dispatch('setCurrentTitle',this.param.title);
          this.$router.push({ name: this.name, params: this.param });
        }else{
          // go license page
          this.$parent.show_model=true;
          console.log("not have lincese");
        }
      }
    }
  },
  props: {
    free: Boolean,
    name: String,
    text: String,
    param: Object,    
  },
  computed: {
    check: function(){
      return !this.free && !this.$store.state.haveLicense;
    }
  }
}
</script>