<script setup>
import DefaultLayout from '@/layout/DefaultLayout.vue';
import MultipleChoice1 from '@/components/Moji/MultipleChoice1.vue';
import MultipleChoice2 from '@/components/Moji/MultipleChoice2.vue';
import MultipleChoice1Result from '@/components/Result/MultipleChoice1Result.vue';
import MultipleChoice2Result from '@/components/Result/MultipleChoice2Result.vue';
import MyButton from '@/components/Button/Button.vue';
import { isEmpty, sleep } from '@/helper';
import ActionTypes from '@/store/actionTypes';
import { computed, onMounted, onUnmounted , reactive, ref } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { useStore } from 'vuex';
const route = useRoute();
const router = useRouter();
const store = useStore();
const currentTitle = store.getters.getCurrentTitle;
var question = reactive({
    moji: {},
    isEnd: false
});
var result = reactive({
    score: 0,
    payMarks: 0,
    yourMarks: 0,
});
const props = defineProps ({
    timeleft: {
        Type: Number,
        default:-1
    }  
})
const finish = ()=>{
    question.isEnd=true;
    window.scrollTo(0, 0);
    mark();
}
const mark = () => {
    let mark=0;
    let payMark=0;     
    let checkPoint = [
        { point: 1, type: 'mondai1' },
        { point: 1, type: 'mondai2' },
        { point: 2, type: 'mondai3' },
        { point: 2, type: 'mondai4' },
        { point: 2, type: 'mondai5' },
    ];
    checkPoint.forEach( check=>{
        question.moji[check.type].questions.forEach( ans => {
            payMark+=check.point;
            if (ans.pick===ans.true) mark+=check.point;
        });
    })
    console.log(`yourmark: ${mark} , payMark: ${payMark}`);
    result.yourMarks = mark;
    result.payMarks = payMark;
    result.score = Math.round( (mark*60)/payMark );
    let scores = store.getters.getScores;
    scores[store.getters.getCurrentYear].vocab_knowledge =  Math.round( (mark*60)/payMark );
    store.dispatch(ActionTypes.setScores, scores);
 }
const seeScore = ()=>{
    router.push('score');
}
onMounted(async () => {
    store.dispatch(ActionTypes.getMoji)
    .then( moji=> {
        question.moji = moji;
    });
    console.log(new Date().getMilliseconds());
    while (store.getters.getTimeLeft> 0) {
        await sleep(1000);
        store.dispatch(ActionTypes.setTimeLeft,store.getters.getTimeLeft-1)                
    }
    finish();
});
const onClose = () => {
    store.dispatch(ActionTypes.setTimeLeft, -1);
}
const getTime = computed(()=> {
    let time = store.getters.getTimeLeft;
    let hh = Math.floor( time/60 );
    let mm = time%60;
    let format = hh+ (mm<10? ':0' : ':') +mm;
    return format;    
})
</script>
<template>
<DefaultLayout 
  :title="' 文字語彙 '+ currentTitle"
  :centerTitle="true" 
  backRoute="category" 
  styles="pt-14 mx-auto md:container "
  :callBack="onClose"
>       
    <div v-if="!isEmpty(question.moji)">
        <div v-if="!question.isEnd" class="mb-32 md:mb-44 ">
            <div class="flex flex-row fixed right-0 md:right-4 h-14 text-white items-center justify-center">
                <div class="flex-1 text-lg text-right select-none  " v-if="store.getters.getTimeLeft>0">
                    <div class=" animate-pulse -mt-[70px] px-5 select-none">{{getTime}}</div>
                </div>
            </div>            
            <MultipleChoice1 :data="question.moji.mondai1"/>
            <MultipleChoice1 :data="question.moji.mondai2"/>
            <MultipleChoice1 :data="question.moji.mondai3"/>
            <MultipleChoice2 :data="question.moji.mondai4"/>
            <MultipleChoice2 :data="question.moji.mondai5"/>
            <div class="flex flex-row justify-center">
                <MyButton :primary="false" title="Finish" :onPress="finish" /> 
            </div>
        </div>
        <div v-else class="mb-32 md:mb-44">
            <div class="flex flex-col justify-center items-center my-10">
                <div :class="result.score>18? 'outline-green-500':'outline-red-500'" class=" -rotate-[33deg] my-6 w-[120px] h-[120px] outline outline-offset-2 outline-8 rounded-full flex flex-col justify-center items-center"> 
                    <div class="font-bold text-2xl ml-4 mb-2" >
                        {{ result.yourMarks }}<span class="text-sm font-bold ml-1">marks</span>
                    </div>
                    <div class="w-[100px] h-1 bg-black">
                    </div>
                    <div class="font-bold text-2xl">
                        {{ result.payMarks }}<span class="text-sm font-bold ml-1">marks</span>                   
                    </div>
                </div>
                <div class="text-xl mt-2" :class="result.score>18? 'text-green-500':'text-red-500'">
                    Score: <span class="font-bold">{{ result.score }}</span> points
                </div>
                <div class="text-sm md:text-xl mt-2" :class="result.score>18? 'text-green-500':'text-red-500'">
                    {{ result.score>18? '🎉　おめでとうございました！　🎉': '💔　ごめんね、あきらめないで、やりなおして！　💔' }}  
                </div>
            </div>
            
            <MultipleChoice1Result :data="question.moji.mondai1"/>
            <MultipleChoice1Result :data="question.moji.mondai2"/>
            <MultipleChoice1Result :data="question.moji.mondai3"/>
            <MultipleChoice2Result :data="question.moji.mondai4"/>
            <MultipleChoice2Result :data="question.moji.mondai5"/>
            <div class="flex flex-row justify-center">
                <MyButton title="See Score" :onPress="seeScore" />
            </div>
        </div>        
    </div>
</DefaultLayout>
</template>
