<script setup>
import DefaultLayout from '@/layout/DefaultLayout.vue';
import BunpouMultiple1 from '../components/Bunpou/BunpouMultiple1.vue';
import MultipleStar from '../components/Bunpou/MultipleStar.vue';
import bunpou from '@/store/questions/j2010_07/bunpou';
import MultipleParagraph from '@/components/Bunpou/MultipleParagraph.vue';
import MultipleParagraph1 from '@/components/Bunpou/MultipleParagraph1.vue';
import ResultMultipleParagraph from '@/components/Bunpou/Result/ResultMultipleParagraph.vue';
import ResultMultipleParagraph1 from '@/components/Bunpou/Result/ResultMultipleParagraph1.vue';
import ResultBunpouMultiple1 from '../components/Bunpou/Result/ResultBunpouMultiple1.vue';
import ResultMultipleStar from '../components/Bunpou/Result/ResultMultipleStar.vue';
import MyButton from '@/components/Button/Button.vue'
import { isEmpty, sleep } from '@/helper';
import ActionTypes from '@/store/actionTypes';
import { computed, onMounted, onUnmounted , reactive, ref } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { useStore } from 'vuex';
const route = useRoute();
const router = useRouter();
const store = useStore();
const currentTitle = store.getters.getCurrentTitle;
var question = reactive({
    bunpou: {},
    isEnd: false
});
var result = reactive({
    score: 0,
    payMarks: 0,
    yourMarks: 0,
});
const props = defineProps({
    timeleft: {
        Type: Number,
        default: -1
    }
});
const finish = () => {
    question.isEnd = true;
    window.scrollTo(0, 0);
    mark();

};
const mark = () => {
    let mark=0;
    let payMark=0;     
    let checkPoint123 = [
        { point: 2, type: 'mondai1', items: 'questions'},
        { point: 2, type: 'mondai2', items: 'questions' },
        { point: 2, type: 'mondai3', items: 'answerBoxs' },
    ];  
     checkPoint123.forEach(check => {
        question.bunpou[check.type][check.items].forEach( ans => {
            payMark+=check.point;
            if (ans.pick===ans.true) mark+=check.point;
        });
    }); 
   let checkPoint4567 = [
        { point: 3, type: 'mondai4', items: 'questions' },
        { point: 3, type: 'mondai5', items: 'questions' },
        { point: 3, type: 'mondai6', items: 'questions' },
        { point: 4, type: 'mondai7', items: 'questions' },
    ];   
    checkPoint4567.forEach(check => {
        question.bunpou[check.type][check.items].forEach( myQuestion => {
            myQuestion.answerBoxs.forEach(ans => {
                payMark+=check.point;
                if (ans.pick===ans.true) mark+=check.point;
            });                
        })          
    });   
    result.yourMarks = mark;
    result.payMarks = payMark;
    let scores = store.getters.getScores;
    scores[store.getters.getCurrentYear].grammer_reading =  Math.round( (mark*60)/payMark );
    store.dispatch(ActionTypes.setScores, scores);
    result.score = Math.round( (mark*60)/payMark);
}
const seeScore = () => {
    router.push('score');
}
onMounted(async () => {
    store.dispatch(ActionTypes.getBunpou)
    .then( bunpou=> {
        question.bunpou = bunpou;
    });
    console.log(new Date().getMilliseconds());
    while (store.getters.getTimeLeft> 0) {
        await sleep(1000);
        store.dispatch(ActionTypes.setTimeLeft, store.getters.getTimeLeft - 1);                
    }
    finish();  
});
const onClose = () => {
    store.dispatch(ActionTypes.setTimeLeft, -1);               
}
const getTime = computed(()=> {
      let time = store.getters.getTimeLeft;
      let hh = Math.floor( time/60 );
       let mm = time%60;
      let format = hh+ (mm<10? ':0' : ':') +mm;
      return format;    
})
    
</script>
<template>
<DefaultLayout 
  :title="'文法 ・ 読解 ' + currentTitle"
  :centerTitle="true" 
  backRoute="category"
  styles="pt-14 mx-auto md:container "
  :callBack="onClose"
>

   <div v-if="!isEmpty(question.bunpou)">
        <div v-if="!question.isEnd" class="mb-32 md:mb-44 ">
            <div class="flex flex-row fixed right-4 md:right-8 h-14 text-white items-center justify-center">
                <div class="flex-1 text-lg text-right select-none  " v-if="store.getters.getTimeLeft>0">
                    <div class=" animate-pulse -mt-[70px]  select-none">{{getTime}}</div>
                </div>
            </div>
            <BunpouMultiple1 :data="question.bunpou.mondai1"/>
            <MultipleStar :data="question.bunpou.mondai2"/>
            <MultipleParagraph :data="question.bunpou.mondai3"/>
            <MultipleParagraph1 :data="question.bunpou.mondai4"/>
            <MultipleParagraph1 :data="question.bunpou.mondai5"/>
            <MultipleParagraph1 :data="question.bunpou.mondai6"/> 
            <MultipleParagraph1 :data="question.bunpou.mondai7"/> 
            <div class="flex flex-row justify-center mt-10">
                <MyButton :primary="false" title="Finish" :onPress="finish" />
            </div>
        </div>
        <div v-else class="mb-32 md:mb-44">
            <div class="flex flex-col justify-center items-center my-10">
                <div :class="result.score>18? 'outline-green-500':'outline-red-500'" class="-rotate-[33deg] my-6 w-[120px] h-[120px] outline outline-offset-2 outline-8 rounded-full flex flex-col justify-center items-center"> 
                    <div class="font-bold text-2xl ml-4 mb-2" >
                        {{ result.yourMarks }}<span class="text-sm font-bold ml-1">marks</span>
                    </div>
                    <div class="w-[100px] h-1 bg-black">
                    </div>
                    <div class="font-bold text-2xl">
                        {{ result.payMarks }}<span class="text-sm font-bold ml-1">marks</span>                   
                    </div>
                </div>
                <div class="text-xl mt-2" :class="result.score>18? 'text-green-500':'text-red-500'">
                    Score: <span class="font-bold">{{ result.score }}</span> points
                </div>
                <div class="text-sm md:text-xl mt-2" :class="result.score>18? 'text-green-500':'text-red-500'">
                    {{ result.score>18? '🎉　おめでとうございました！　🎉': '💔　ごめんね、あきらめないで、やりなおして！　💔' }}  
                </div>
            </div>
           <ResultBunpouMultiple1 :data="question.bunpou.mondai1"/> 
           <ResultMultipleStar :data="question.bunpou.mondai2"/> 
           <ResultMultipleParagraph :data="question.bunpou.mondai3"/> 
           <ResultMultipleParagraph1 :data="question.bunpou.mondai4"/> 
           <ResultMultipleParagraph1 :data="question.bunpou.mondai5"/> 
           <ResultMultipleParagraph1 :data="question.bunpou.mondai6"/>  
           <ResultMultipleParagraph1 :data="question.bunpou.mondai7"/>  
            <div class="flex flex-row justify-center">
                <MyButton  title="See Score" :onPress="seeScore" />
            </div>
        </div>        
    </div>
</DefaultLayout>
</template>
