<script setup>
import { mdiChevronLeft ,mdiChevronRight } from '@mdi/js';
import Icon from '@/components/Svg.vue';
import DefaultLayout from '@/layout/DefaultLayout.vue';
import MyButton from '@/components/Button/Button.vue';
import { isEmpty, sleep } from '@/helper';
import ActionTypes from '@/store/actionTypes';
import { computed, onMounted, onUnmounted , reactive, ref } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { useStore } from 'vuex';
import MainTitle from '@/components/choukai/MainTitle.vue';
import Listening1 from '@/components/choukai/Listening1.vue';
import Listening2 from '@/components/choukai/Listening2.vue';
import Listening3 from '@/components/choukai/Listening3.vue';
import Listening4 from '@/components/choukai/Listening4.vue';
import Listening5 from '@/components/choukai/Listening5.vue';
import ListeningResult1 from '@/components/choukai/result/ListeningResult1.vue';
import ListeningResult2 from '@/components/choukai/result/ListeningResult2.vue';
import ListeningResult3 from '@/components/choukai/result/ListeningResult3.vue';
import ListeningResult4 from '@/components/choukai/result/ListeningResult4.vue';
import ListeningResult5 from '@/components/choukai/result/ListeningResult5.vue';
const route = useRoute();
const router = useRouter();
const store = useStore();
const year = store.getters.getCurrentYear;
const currentTitle = store.getters.getCurrentTitle;
var audio = new Audio();
let isNext = true;
let useAction = false;
var question = reactive({
    choukai: {},
    parent: '',
    child: '',
    index: 0
});

var result = reactive({
    score: 0,
    payMarks: 0,
    yourMarks: 0,
});
onUnmounted(()=>onClose());
onMounted(async () => {
    await store.dispatch(ActionTypes.getChoukai)
    .then(choukai => {
        question.choukai = choukai;
    });
    start();
});
// testing purpose
const next = () => {
    useAction=true;
    let schedule = question.choukai.schedule;   
    if (question.index < schedule.length - 1) {
        isNext = true;
        audio.pause();       
    } else {
        onClose();
        question.parent = 'finished';
        question.child = '';
        mark();
    }
}
const previous = ()=>{
    if(question.index==0) return;
    useAction=true;
    isNext = false;
    audio.pause();
} 
// testing purpose
const onClose = ()=>{
    question.index = 100;
    if(audio.duration > 0 && !audio.paused){
        // release
        // audio.pause();
        // release

        // dev purpose
        audio.src = null;
        // dev purpose
    }
}
const mark = () => {
    let mark = 0;
    let payMark = 0;
      let checkPoint = [
        { point: 1, type: 'mondai1' },
        { point: 1, type: 'mondai2' },
        { point: 1, type: 'mondai3' },
        { point: 1, type: 'mondai4' },
        { point: 2, type: 'mondai5' },
    ];

    checkPoint.forEach(check => {
        question.choukai[check.type].questions.forEach(ans => {
            payMark+=check.point;
            if (ans.pick === ans.true) mark+=check.point;
        });
    }); 
    
    console.log(`yourmark: ${mark} , payMark: ${payMark}`); 
    result.yourMarks = mark;
    result.payMarks = payMark;
    result.score = Math.round( (mark*60)/payMark );
    let scores = store.getters.getScores;
    scores[store.getters.getCurrentYear].listening =  Math.round( (mark*60)/payMark );
    store.dispatch(ActionTypes.setScores, scores);    
}
const seeScore = () => {
    router.push('score');
}
const start = async ()=> {
    let schedule = question.choukai.schedule;
    console.log(question.index);
    let parent = schedule[question.index].parent;
    let child = schedule[question.index].child;
    let delay = schedule[question.index].delay;
    let url = schedule[question.index].url;
    
    question.parent = parent;
    question.child = child;

    // // Testing Purpose    >>>>>>>
    // await sleep(3000);
    // if(index < schedule.length-1){
    //   index++;
    //   start();
    // }else{
    //   console.log('finish');
    // }
    // // Testing Purpose    >>>>>>>
    // play sound         <<<<<<<
    const songUrl = require(`@/assets/${year}/${url}`);
    audio.autoplay = true;
    audio.src = songUrl;
    audio.load();
    // audio.play();

    audio.onpause = async () => {
        if( question.index < schedule.length-1 ){
            if(useAction){
                if(isNext){
                    question.index++;
                }else{
                    question.index--;
                    isNext=true;
                }
                useAction=false;
            }else{
                await sleep(delay);
                question.index++;
            }
            start();
        }else{
            question.parent = 'finished';
            question.child = '';
            mark();
        }
    };

    // next-previous purpose
}
</script>
<template>
    <DefaultLayout 
        :title="' 聴解 ' + currentTitle"
        :centerTitle="true" 
        backRoute="category"
        styles="pt-14 mx-auto md:container md:px-24 "
        :callBack="onClose"
    >
        <div v-if="question.parent!='finished' && !isEmpty(question.choukai)" class="flex flex-row justify-between mt-6 text-white">
            <div @click="previous" :class="question.index==0? 'cursor-not-allowed opacity-0': 'cursor-pointer' " class="flex flex-row justify-center px-4 py-2 bg-blue-500 w-fit select-none">
                <Icon :path="mdiChevronLeft" />Previous
            </div>
            <div @click="next" class="flex flex-row justify-center px-4 py-2 bg-cyan-500 w-fit select-none cursor-pointer">
                next<Icon :path="mdiChevronRight" />
            </div>
        </div>
        <div v-if="!isEmpty(question.choukai)">
            <MainTitle :parent="question.parent" :title="currentTitle" />
            <Listening1 :parent="question.parent" :child="question.child" :choukai="question.choukai" :year="year" />
            <Listening2 :parent="question.parent" :child="question.child" :choukai="question.choukai" :year="year" />
            <Listening3 :parent="question.parent" :child="question.child" :choukai="question.choukai" :year="year" />
            <Listening4 :parent="question.parent" :child="question.child" :choukai="question.choukai" :year="year" />
            <Listening5 :parent="question.parent" :child="question.child" :choukai="question.choukai" :year="year" />

        </div>
        <!-- result section -->
        <div v-if="question.parent=='finished' && !isEmpty(question.choukai)" class="mb-18">
            <div class="flex flex-col justify-center items-center my-10 z-0">
                <div :class="result.score>18? 'outline-green-500':'outline-red-500'" class="-rotate-[33deg] my-6 w-[120px] h-[120px] outline outline-offset-2 outline-8 rounded-full flex flex-col justify-center items-center"> 
                    <div class="font-bold text-2xl ml-4 mb-2" >
                        {{ result.yourMarks }}<span class="text-sm font-bold ml-1">marks</span>
                    </div>
                    <div class="w-[100px] h-1 bg-black">
                    </div>
                    <div class="font-bold text-2xl">
                        {{ result.payMarks }}<span class="text-sm font-bold ml-1">marks</span>                   
                    </div>
                </div>
                <div class="text-xl mt-2" :class="result.score>18? 'text-green-500':'text-red-500'">
                    Score: <span class="font-bold">{{ result.score }}</span> points
                </div>
                <div class="text-sm md:text-xl mt-2" :class="result.score>18? 'text-green-500':'text-red-500'">
                    {{ result.score>18? '🎉　おめでとうございました！　🎉': '💔　ごめんね、あきらめないで、やりなおして！　💔' }}  
                </div>
            </div>
            <ListeningResult1 parent="mondai1" :choukai="question.choukai" :year="year"/>
            <ListeningResult2 parent="mondai2" :choukai="question.choukai" />
            <ListeningResult3 parent="mondai3" :choukai="question.choukai" />
            <ListeningResult4 parent="mondai4" :choukai="question.choukai" :year="year"/>
            <ListeningResult5 parent="mondai5" :choukai="question.choukai"/>
            <!-- See Score button here -->
            <div class="flex flex-row justify-center mb-32 md:mb-44">
                <MyButton :primary="false" title="See Score" :onPress="seeScore" />
            </div>
        </div>
    </DefaultLayout>
</template>
