<script setup>
import DefaultLayout from '@/layout/DefaultLayout.vue';
import Icon from '@/components/Svg.vue';
import { mdiTrophyOutline, mdiEmoticonCry,mdiHeartBroken} from '@mdi/js';
import MyButton from '@/components/Button/Button.vue'
import { isEmpty } from '@/helper';
import ActionTypes from '@/store/actionTypes';
import { computed, onMounted, onUnmounted , reactive, ref } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { useStore } from 'vuex';
const route = useRoute();
const router = useRouter();
const store = useStore();
const currentTitle = store.getters.getCurrentTitle;
const scores = store.getters.getScores;
const vocab_knowledge = scores[store.getters.getCurrentYear].vocab_knowledge;
const grammer_reading = scores[store.getters.getCurrentYear].grammer_reading;
const listening = scores[store.getters.getCurrentYear].listening;

const pass = () => {
  return ( vocab_knowledge > 18 && grammer_reading > 18 && listening > 18 ) 
    && 
  total() > 89;
}
const fail = () => {
    return ( vocab_knowledge != -1 && grammer_reading != -1 && listening != -1 )
    &&
    ( 
      ( vocab_knowledge < 19 || grammer_reading < 19 || listening < 19 )
      ||  
      total() < 90 
    );
}

const total=() =>{
      let total;
      if( vocab_knowledge > -1 && grammer_reading >-1 && listening > -1 ){
        total = vocab_knowledge + grammer_reading + listening;
      }else{
        total=-1;
      }
      return total;
}
</script>
<template>    
    <DefaultLayout 
      :title="' RESULT '+ currentTitle"
      :centerTitle="true" 
      backRoute="category"
      styles="pt-14 mx-auto container "
    >
    <div class="mx-3 my-6">
      <div class="text-center font-mono text-sm md:text-2xl font-bold pt-4">JAPANESE-LANGUAGE PROFICIENCY TEST</div>
      <div class="text-center font-mono text-lg font-bold pt-1">N3</div>
      <div class="text-center font-mono text-sm pt-1"> Old Questions</div>
      <div class="text-center font-mono text-lg font-bold pt-1">RESULT AND SCORES</div>
    </div>    
        <!-- head -->
      <div class="flex flex-row ">
        <div class="flex flex-1 h-14 bg-white text-center items-center justify-center text-sm font-bold border-2 border-white">
        </div>
        <div class="flex flex-col flex-1 h-14 bg-cyan-400 text-center items-center justify-center text-sm font-bold border-2 border-white">
          <div>Knowledge</div>
          <div>(Vocab)</div>
        </div>
        <div class="flex flex-col flex-1 h-14 bg-pink-300 text-center items-center justify-center text-sm font-bold border-2 border-white">
          <div>Grammar</div>
          <div>Reading</div>
        </div>
        <div class="flex flex-1 h-14 bg-blue-400 text-center items-center justify-center text-sm font-bold border-2 border-white">
          <div>Listening</div>
        </div>
      </div>
      <!-- head -->

      <!-- Row1 -->
      <div class="flex flex-row">
        <div class="flex flex-1 h-14 bg-purple-100 text-center items-center justify-center text-sm font-bold border-2 border-white">
          <div>Range of Scores</div>
        </div>
        <div class="flex flex-col flex-1 h-14 bg-cyan-200 text-center items-center justify-center text-sm border-2 border-white">
          <div>0~60</div>
          <div>points</div>
        </div>
        <div class="flex flex-col flex-1 h-14 bg-pink-100 text-center items-center justify-center text-sm border-2 border-white">
          <div>0~60</div>
          <div>points</div>
        </div>
        <div class="flex flex-col flex-1 h-14 bg-blue-200 text-center items-center justify-center text-sm border-2 border-white">
          <div>0~60</div>
          <div>points</div>
        </div>
      </div>
      <!-- Row1 -->
      
      <!-- Row2 -->
      <div class="flex flex-row">
        <div class="flex flex-1 h-14 bg-purple-100 text-center items-center justify-center text-red-500 text-sm font-bold border-2 border-white">
          <div>Sectional Pass Marks</div>
        </div>
        <div class="flex flex-col flex-1 h-14 bg-cyan-200 text-center items-center justify-center text-sm border-2 border-white">
          <div>19</div>
          <div>points</div>
        </div>
        <div class="flex flex-col flex-1 h-14 bg-pink-100 text-center items-center justify-center text-sm border-2 border-white">
          <div>19</div>
          <div>points</div>
        </div>
        <div class="flex flex-col flex-1 h-14 bg-blue-200 text-center items-center justify-center text-sm border-2 border-white">
          <div>19</div>
          <div>points</div>
        </div>
      </div>
      <!-- Row2 -->
      <!-- Row3 -->
      <div class="flex flex-row">
        <div class="flex flex-none w-1/4 h-14 bg-purple-100 text-center items-center justify-center text-red-500 text-sm font-bold border-2 border-white">
          <div>Overall Pass Marks</div>
        </div>
        <div class="flex flex-col flex-1 h-14 bg-gradient-to-r from-cyan-200 to-blue-200 text-center items-center justify-center text-sm border-2 border-white">
          <div>90</div>
          <div>points</div>
        </div>
      </div>
      <!-- Row3 -->
      <!-- Row4 -->
     <div class="flex flex-row">
        <div class="flex flex-1 h-14 bg-cyan-100 text-center items-center justify-center text-blue-500 text-sm font-bold border-2 border-white">
          <div>Your Scores</div>
        </div>
        <div class="flex flex-col flex-1 h-14 bg-blue-200 text-center items-center justify-center text-sm border-2 border-white">
          <div 
            v-if="vocab_knowledge>=19" 
            class="font-bold text-green-500"
          >
            {{vocab_knowledge}}
          </div>
          <div 
            v-else-if="vocab_knowledge>-1&&vocab_knowledge<19"
            class="font-bold text-red-500"
          >
            {{vocab_knowledge}}
          </div>
          <div v-else> -</div>
          <div>points</div>
        </div>
        <div class="flex flex-col flex-1 h-14 bg-pink-100 text-center items-center justify-center text-sm border-2 border-white">
          <div 
            v-if="grammer_reading>=19" 
            class="font-bold text-green-500"
          >
            {{grammer_reading}}
          </div>
          <div 
            v-else-if="grammer_reading>-1 && grammer_reading<19"
            class="font-bold text-red-500"
          >
            {{grammer_reading}}
          </div>
          <div v-else>-</div>
          <div>points</div>
        </div>
        <div class="flex flex-col flex-1 h-14 bg-blue-200 text-center items-center justify-center text-sm border-2 border-white">
          <div 
            v-if="listening>=19" 
            class="font-bold text-green-500"
          >
            {{listening}}
          </div>
          <div 
            v-else-if="listening >-1 && listening<19"
            class="font-bold text-red-500"
          >
            {{ listening }}
          </div>
          <div v-else>-</div>
          <div>points</div>
        </div>
      </div>
      <!-- Row4 -->
      <!-- Row5 -->
      <div class="flex flex-row">
        <div class="flex flex-none w-1/4 h-14 bg-purple-100 text-center items-center justify-center text-blue-500 text-sm font-bold border-2 border-white">
          <div>Total</div>
        </div>
        <div class="flex flex-col flex-1 h-14 bg-gradient-to-r from-cyan-200 to-blue-200 text-center items-center justify-center text-sm border-2 border-white">
          <div 
            v-if="total()>=90" 
            class="font-bold text-green-500"
          >
            {{total()}}
          </div>
          <div 
            v-else-if="total()>-1&&total()<90"
            class="font-bold text-red-500"
          >
            {{total()}}
          </div>
          <div v-else>-</div>
          <div>points</div>
        </div>
      <!-- Row5 -->     
    </div> 
     <!-- pass -->
      <div v-if="pass()" class="h-14 flex flex-row items-center justify-center bg-gradient-to-r from-yellow-500 to-yellow-300">
        <Icon :path="mdiTrophyOutline" size="20" class="text-xl mx-6"/>
        <div class="font-bold text-xl">
          PASS
        </div>
        <Icon :path="mdiTrophyOutline" size="20" class="text-xl mx-6"/>
      </div>
      
      <!-- fail -->
      <div v-if="fail()" class="h-14 flex flex-row items-center justify-center bg-gradient-to-r from-pink-500 to-pink-100">
        <Icon :path="mdiEmoticonCry" size="20" class="text-xl mx-6 text-blue-900"/>
        <div class="font-bold text-xl text-blue-900">
          Fail
        </div>
        <Icon :path="mdiHeartBroken" size="20" class="text-xl mx-6 text-blue-900"/>
      </div>  
    </DefaultLayout>
</template>