<script setup>
import { mdiCrosshairs, mdiCrosshairsGps } from '@mdi/js';
import { questionLead, titleLead } from '@/helper';
import Icon from '@/components/Svg.vue';
const props = defineProps({
    data: {
        type: Object,
        required: true
    }
});
props.data.questions.forEach( element => element.answers.sort(() => (Math.random() > 0.5) ? 1 : -1) );
</script>

<template>
  <div class="pt-5">
    <!-- mondai title -->
  <div 
    v-html='titleLead(data.title)'
    class="font-bold px-3 pt-2 pb-4 lg:pb-8 text-sm text-justify select-none"
  ></div>

  <!-- question loop -->
  <div
      v-for="(question,index) in data.questions"
      v-bind:key="index"
      class="py-2 text-sm"
    >
      <!-- question title -->

      <div 
        v-html="questionLead(`(${question.number})　${question.question}`)" 
        class="px-3 pb-2 select-none"
      ></div>
      <!-- answer box -->

      <div class="px-5 pb-8 md:pb-14 grid grid-cols-2 gap-2 md:gap-6 select-none">
        <div 
          v-for="(answer,i) in question.answers"
          v-bind:key="i"
        >
          <button
            v-if="question.pick!=answer"
            v-on:click="question.pick=answer"
            class="flex flex-row w-full h-12 px-3 bg-gray-200 items-center justify-start rounded-md">
            <Icon :path="mdiCrosshairs" size="20" class="text-gray-500"/>
            <div class="pl-2 text-xs text-left">{{answer}}</div>
          </button>
          <button 
              v-if="question.pick==answer"
              class="flex flex-row w-full h-12 px-3 font-bold bg-gray-300 items-center justify-start rounded-md">
              <Icon :path="mdiCrosshairsGps" size="20" class="text-blue-500 font-bold"/>
              <div class="pl-2 text-xs　text-left">{{answer}}</div>
          </button>
        </div>
      </div>
    </div>
  </div>
  
</template>