<script setup>
import Appbar from '@/components/AppBar.vue';
import { reactive } from 'vue';

const props = defineProps({
    title: {
      type: String,
      required: false,
      default: ''
    },
    centerTitle: {
      type: Boolean,
      required: false,
      default: true,
    },
    callBack: {
      type: Function,
      required: false,
      default: ()=>{}
    },
    backRoute: {
      type: String,
      required: false,
      default: 'home'
    },
    styles: {
      type: String,
      required: false,
      default: '',
    }    
});
const data = reactive({
  isDrop: false
});
const toggle = ()=>{
  data.isDrop = !data.isDrop;
}
const closeDropdown = ()=> {
  if(data.isDrop) data.isDrop = false;
}
</script>
<template>
    <Appbar 
        :isDrop="data.isDrop" 
        :centerTitle="centerTitle" 
        :title="title" 
        :callBack="callBack"
        :toggle="toggle"
        :backRoute="backRoute"
    />
    <div :class="styles" @click="closeDropdown">
        <slot></slot>
    </div>
</template>