export const startQuestionLead = (question,index) => {
    var myArray = question.split('｜');
      var html='';
      if(index==0){
        html+=`<div class="flex flex-row flex-wrap text-sm">`;
      }else{
        html+=`<div class="flex flex-row flex-wrap text-sm pl-4">`;
      }
      myArray.forEach(e => {
        if(e.includes('！')){
          let subStr = e.substring(1);
          let mySubArray = subStr.split('');
          mySubArray.forEach(s =>{
            if(s=='＿'){
                html+=`<div class="">&nbsp</div>`;
                html+=`<div class="underline ">&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp</div>`;
                html+=`<div class="">&nbsp</div>`;
            }else{
                html+=`<div class="">&nbsp</div>`;
                html+=`<div class="font-bold underline">&nbsp&nbsp${s}&nbsp&nbsp</div>`;
                html+=`<div class="">&nbsp</div>`;
            }
          });
        }else{
          let mySubArray = e.split('');
          mySubArray.forEach(t =>{
            html+=`<div class="">${t}</div>`;
          });
        }
      });
      html+=`</div>`;
      return html;
}

