<script setup>
import { mdiLock } from '@mdi/js';
import Icon from '@/components/Svg.vue';
import actionTypes from '@/store/actionTypes.js';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
const router = useRouter();
const store = useStore();
const props = defineProps({
    isFree: {
        type: Boolean,
        required: true
    },
    title: {
        type: String,
        required: true
    },
    year: {
        type: String,
        required: true
    },
    routeName: {
        type: String,
        required: true
    }
})
const onPress = ()=>{
    // check auth
    store.dispatch(actionTypes.setCurrentYear,props.year)
    store.dispatch(actionTypes.setCurrentTitle,props.title)
    router.push({ name: props.routeName });
}
const checkLicense = ()=>{
    return true;
}
</script>
<template>
    <button 
        @click="onPress()"
        class="w-3/4 md:w-2/3 my-4 py-2 bg-gradient-to-r to-cyan-500 from-blue-500 font-bold text-white text-xl text-center rounded-xl">
        {{title}} <Icon v-if="!isFree" :path="mdiLock" size="20" class="text-yellow-400 text-xl ml-3"/>
    </button>
</template>