<script setup>
import {  mdiCheckboxMarkedCircleOutline, mdiCloseCircleOutline ,mdiCheckboxBlankCircleOutline } from '@mdi/js';
import Icon from '@/components/Svg.vue';
const props = defineProps({
  data: {
      type: Object,
      required: true
  },
});
const catchUrl = (text) => {
 // <img class="w-full bg-gray-500" src="|@assets/j2018/bunpou/m6.png|" alt="">
    var myArray = text.split('|');
    var html='';
    myArray.forEach(e => {
      if(e.includes('@')){
        let url = e.substring(1);
        let full = require('@/'+ url);
        html += full;
      }else{
        html+=e;
      }
    });
    return html;
}
const grid= (ans)=> {
    var overFlow = false;
    ans.forEach(e => {
      if(e.length>10){
        overFlow=true;
      }
    });
    return overFlow?'grid-cols-1':'grid-cols-2';
}
const style =(styles)=> {
    var style = '';
    for (const [key,value] of Object.entries(styles)) {
      style+=value + ' ';
    }
    return style;
}
const titleLead= (title)=> {
      var myArray = title.split('｜');
      var html='';
      html+=`<div class="flex flex-row text-md flex-wrap">`;
      myArray.forEach(ans => {
          if(ans.includes('！')){
              let star = ans.substring(1);             
              html+=`<div class="underline font-bold">&nbsp${star}&nbsp</div>`;
              html+=`<div class="">&nbsp</div>`;
          }else{
              let tmpArray = ans.split('');
              tmpArray.forEach(ele => {
                 html+=`<div>${ele}</div>`; 
              });
          }
      });
      html+=`</div>`;
      return  html;
}
const paragraphs= (text,space=true)=> {
      var myArray = text.split('｜');
      var html='';
      html+=`
        <div class="flex flex-row flex-wrap">
      `;
      if(space){
        html+=`
          <div >&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp</div>
        `;
      }
      myArray.forEach(ans => {
          if(ans.includes('！')){
              let star = ans.substring(1);             
              html+=`<div class="underline font-bold">${star}</div>`;
              html+=`<div class="">&nbsp</div>`;
          }else{
              let tmpArray = ans.split('');
              tmpArray.forEach(ele => {
                 html+=`<div>${ele}</div>`; 
              });
          }
      });
      html+=`</div>`;
      return  html;
  }
</script>
<template>
  <div class="pt-5"></div>
  <!-- mondai title -->
  <div 
    v-html="titleLead(data.title)" 
    class="font-bold px-3 pt-2 pb-4 lg:pb-8 text-sm text-justify select-none"
  ></div>
  <!-- question loop -->
  <div 
    v-for="(question, index) in data.questions" :key="index"
  >
    <!-- title number -->
    <div v-if="question.titleNumber!=''" class="text-lg lg:text-2xl text-center py-1 select-none">{{question.titleNumber}}</div>
    <!-- explain -->
    <div v-if="question.explain!=''" class="mx-3 text-sm py-1 select-none">{{question.explain}}</div>
    <!-- paragraphs -->
    <div
      v-for="(paragraph, p) in question.paragraphs" :key="p"
    >
      <div 
        class="mx-3 text-sm py-1 select-none leading-loose"
        v-html="paragraphs(paragraph)"
      >
      </div>
    </div>
    <!-- Box Loop -->
    <div v-for="(box, b) in question.boxs" :key="b">
      <!-- box border -->
      <div class="mx-5 my-5 py-4 lg:py-8 px-2 lg:px-8 border-2 border-gray-500 select-none">
        <!-- title -->
        <div 
          v-for="(title,index) in box.title.contents"
          v-bind:key="index"
          v-bind:class="style(box.title.style)"
        >
          <div>{{title}}</div>
        </div>
        <!-- subTitle -->
        <div 
          v-for="(subTitle,index) in box.subTitle.contents"
          v-bind:key="index"
          v-bind:class="style(box.subTitle.style)"
        >
          <div>{{subTitle}}</div>
        </div>
        
        <!-- contents -->
        <div
          v-for="(content, index) in box.contents" :key="index"
          v-bind:class="style(content.style)"
        >
          <div
            v-for="(text, index) in content.texts" :key="index"
            v-html="paragraphs(text,content.space)"
          > 
            
          </div>
        </div>
        <!-- tail -->
        <div 
          v-for="(tail,index) in box.tail.contents"
          v-bind:key="index"
          v-bind:class="style(box.tail.style)"
        >
          <div>{{tail}}</div>
        </div>
      </div>
    </div>
    <!-- answer -->
    <div class="my-3"></div>
    <!-- number and title -->
    <div
      v-for="(answerItem, a) in question.answerBoxs" :key="a"
    >
      <div 
        v-html="titleLead(answerItem.title)"
        class="mx-3 py-4 text-sm font-semibold select-none"
      ></div>
      <!-- button -->
      <div v-bind:class="grid(answerItem.answers)" class="px-5 pb-4 grid gap-2 md:gap-6 select-none">
        <div 
          v-for="(answer,i) in answerItem.answers"
          v-bind:key="i"
        >
          <div
            v-if="answerItem.pick==answer&&answerItem.true==answer"
            class="flex flex-row w-full h-12 px-3 bg-green-100 items-center justify-start rounded-md">
            <Icon :path="mdiCheckboxMarkedCircleOutline" size="20" class="text-green-500"/>
            <div class="pl-2 text-xs font-bold text-left">{{answer}}</div>
          </div>
          <div
            v-if="answerItem.pick==answer&&answerItem.true!=answer"
            class="flex flex-row w-full h-12 px-3 bg-red-100 items-center justify-start rounded-md">
            <Icon :path="mdiCloseCircleOutline" size="20" class="text-red-500"/>
            <div class="pl-2 text-xs font-bold text-left">{{answer}}</div>
          </div>
          <div
            v-if="answerItem.pick!=answer&&answerItem.true!=answer"
            class="flex flex-row w-full h-12 px-3 bg-gray-200 items-center justify-start rounded-md">
            <Icon :path="mdiCheckboxBlankCircleOutline" size="20" class="text-gray-500"/>
            <div class="pl-2 text-xs text-left">{{answer}}</div>
          </div>
          <div
            v-if="answerItem.pick!=answer&&answerItem.true==answer"
            class="flex flex-row w-full h-12 px-3 bg-gray-200 items-center justify-start rounded-md">
            <Icon :path="mdiCheckboxMarkedCircleOutline" size="20" class="text-green-500"/>
            <div class="pl-2 text-xs text-left">{{answer}}</div>
          </div>
        </div>
      </div>
      <div class="my-3"></div>
    </div>

    <!-- HTML Injection For Custom -->
    <div 
      v-if="question.html!=``" 
      v-html="catchUrl(question.html)"
      class="select-none"
    ></div>
  </div>  
  
  <!-- calculate result -->
  <!-- <div v-if="mondai=='mondai6'" class="flex flex-row items-center justify-center my-12">
    <button 
      @click="$parent.setScore()"
      class="px-6 my-4 py-2 bg-gradient-to-r from-indigo-500 to-blue-500 font-bold text-white text-xl text-center rounded-xl">
          See Score
    </button>
  </div> -->
</template>

