import { createRouter, createWebHashHistory } from 'vue-router';
import HomeView from '../views/HomeView.vue';
import AboutView from '../views/AboutView.vue';
import CategoryView from "../views/CategoryView.vue";
import MojiView from "../views/MojiVeiw.vue";
import BunpouView from "../views/BunpouView.vue";
import ChoukaiView from "../views/ChoukaiView.vue";
import ScoreView from "../views/ScoreView.vue";
import LoadingView from "../views/LoadingView.vue";
const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView
  },
  {
    path: '/category',
    name: 'category',
    component: CategoryView
  },
  {
    path: '/moji',
    name: 'moji',
    component: MojiView
  },
  {
    path: '/bunpou',
    name: 'bunpou',
    component: BunpouView
  }, 
  {
    path: '/score',
    name: 'score',
    component: ScoreView
  }, 
  {
    path: '/choukai',
    name: 'choukai',
    component: ChoukaiView
  },  
  {
    path: '/loading',
    name: 'Loading',
    component: LoadingView
  }
];
const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router;
