<script setup>
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
import actionsTypes from '@/store/actionTypes';
const store = useStore();
const router = useRouter();
const props = defineProps({
  routeName: {
    type: String,
    required: true
  },
  title: {
    type: String,
    required: true
  }
});
const furigana = ()=>{
  let temp = props.title;
  const kanji = temp.split("｜"); 
  var html = '';
  html+='<div class="flex flex-row flex-wrap justify-center">';
  kanji.forEach(element => {
    if(element.includes("＾")){
      html+=`
        <div class="flex flex-col justify-end">
          <div class="text-xs pr-1px -mb-0.5">${element.substring(2)}</div>
          <div class="text-2xl">${element[0]}</div>
        </div>
      `;
    }else{
      html+=`
        <div class="flex flex-col justify-end">
          <div class="text-2xl">${element}</div>
        </div>
      `;
    }
  });
  html+='</div>';
  return html;
};
const toRoute = () => {
  if(props.routeName=='moji'){
    store.dispatch(actionsTypes.setTimeLeft,1800);
    // router.push({ name: props.routeName });
    router.push({ name: 'Loading', params: {routeName: props.routeName} });
  }else if (props.routeName == 'bunpou') {
    store.dispatch(actionsTypes.setTimeLeft,4200);
    // router.push({ name: props.routeName });
    router.push({ name: 'Loading', params: {routeName: props.routeName} });
  }else{
    store.dispatch(actionsTypes.setTimeLeft, -1);
    // router.push({ name: props.routeName });
    router.push({ name: 'Loading', params: {routeName: props.routeName} });
  }
}
</script>
<template>
     <button 
      @click="toRoute()"
      class="w-3/4 md:w-2/3 mx-10 my-4 py-2 bg-gradient-to-r to-cyan-500 from-blue-500 font-bold text-white text-xl text-center rounded-xl"
      v-html="furigana()"
    >
    </button>    
</template>